@import url(https://fonts.googleapis.com/css2?family=Abril+Fatface&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Abril+Fatface&family=Roboto+Condensed:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Abril+Fatface&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Abril+Fatface&family=Roboto+Condensed:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Abril+Fatface&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Abril+Fatface&family=Roboto+Condensed:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html {
  scroll-behavior: smooth;
}
* {
  box-sizing: border-box;
}
.App {
  padding: 0;
  margin: 0;
  text-align: center;
}

.home {
  padding: 0;
  margin: 0;
  background-color: transparent;
}
iframe {
  width: 100%;
  height: 700px;
  margin: 0;
  padding: 0;
  display: flex;
  background-size: cover;
  background-repeat: no-repeat;
  border-bottom-left-radius: 20%;
  border-bottom-right-radius: 20%;
}
.content {
  top: 0;
  left: 0;
  width: 100%;
  height: -webkit-max-content;
  height: -moz-max-content;
  height: max-content;
  position: absolute;
}

.user {
  padding-top: 80px;
  padding-left: 100px;
  padding-right: 100px;
  display: flex;
  justify-content: space-around;
}
.user-name {
  font-family: "Abril Fatface", cursive;
  font-size: 70px;
  align-items: center;
  text-align: center;

  color: white;
}
.user-name h1 {
  position: relative;
  color: transparent;
  -webkit-animation: text_reveal 0.5s ease forwards;
          animation: text_reveal 0.5s ease forwards;
  -webkit-animation-delay: 2s;
          animation-delay: 2s;
}
.user-name h1 span {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 0%;
  background-color: crimson;
  -webkit-animation: text_reveal_box 1s ease;
          animation: text_reveal_box 1s ease;
  -webkit-animation-delay: 1.5s;
          animation-delay: 1.5s;
}
.user-pic {
  height: 400px;
  width: 400px;
  border-radius: 50%;
  background-image: url(/react-portfolio/static/media/user.e72a1587.jpg);
  background-size: cover;
}
.user-name p {
  font-size: 20px;
  font-family: "Roboto Condensed", sans-serif;
  align-items: center;
  text-align: center;
}
.user-name hr {
  margin-top: -5px;
  margin-left: 80px;
  margin-right: 80px;
  border: 3px solid red;
}

@-webkit-keyframes text_reveal_box {
  50% {
    width: 100%;
    left: 0;
  }
  100% {
    width: 0%;
    left: 100%;
  }
}

@keyframes text_reveal_box {
  50% {
    width: 100%;
    left: 0;
  }
  100% {
    width: 0%;
    left: 100%;
  }
}

@-webkit-keyframes text_reveal {
  100% {
    color: white;
  }
}

@keyframes text_reveal {
  100% {
    color: white;
  }
}
@media screen and (max-width: 480px) {
  .front-page {
    display: flex;
    flex-direction: column;
  }
  .user {
    display: flex;
    flex-direction: column;
  }
  .user-pic {
    width: 75%;
  }
}

.header {
  padding: 5px 10px;
  display: flex;
  justify-content: space-around;
}
.logo {
  padding-top: 10px;
  text-align: center;
}
.nav-bar {
  float: right;
  padding: 10px 20px;
}
nav {
  border-radius: 15px;
  background-color: transparent;
}
.nav-wrapper {
  padding: 0 10px;
  opacity: 1;
  display: flex;
}
a {
  color: white !important;
  font-size: 100px;
  font-weight: bold;
}
li {
  justify-content: space-around;
}
a:hover,
a:active {
  color: white !important;
  font-weight: bold;
  border-bottom: 3px solid white;
}
@media screen and (max-width: 480px) {
  .header {
    display: flex;
    flex-flow: column-reverse;
  }
  
}

.skills {
  margin: 0;
  padding: 0;
}
.skills-header {
  text-align: center;
}
.skills-header h1 {
  font-family: "Abril Fatface", cursive;
}
.skills-header hr {
  margin: 0 42%;
  border: 3px solid red;
}
.skill-content {
  display: flex;
  margin: 0 50px;
  padding: 30px 20px;
  border-left: 3.5px solid black;
  border-right: 3.5px solid black;
  justify-content: space-around;
}

.about {
  padding: 20px 20px;
  margin: 0;
}
.about-header {
  text-align: center;
}
.about-header h1 {
  font-family: "Abril Fatface", cursive;
}
.about-header hr {
  margin: 0 42%;
  border: 3px solid red;
}
.about-items {
  padding: 20px 10px;
  width: 100%;
  height: -webkit-min-content;
  height: -moz-min-content;
  height: min-content;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.card {
  padding: 10px 15px;
  width: calc(250px * 1.586);
  height: 250px;

  border-radius: 0.75rem;
  box-shadow: 0 22px 70px 4px rgba(0, 0, 0, 0.56), 0 0 0 1px rgba(0, 0, 0, 0.3);

  background: black;

  display: flex;
  grid-template-columns: 40% auto;
  color: white;

  align-items: center;
}
.name {
  font-family: "Courier New", Courier, monospace;
  font-size: 20px;
  font-weight: 600;
  color: purple;
}
.card-about {
  margin-top: 1rem;
  display: flex;
  grid-auto-flow: column;
  justify-content: space-between;
}
.card-details {
  width: 275px;
  height: 151px;
}
.item {
  display: inline-flex;
  flex-direction: column;
  margin-bottom: 0.5rem;
  justify-content: space-between;
}
.skills {
  display: flex;
  flex-direction: column;
  margin-top: 0.75rem;
}
.label {
  margin-top: 0.15rem;
  font-size: 0.75rem;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  font-size: 1rem;
  font-weight: 600;
  color: purple;
}

.value {
  margin-top: 0.15rem;
  font-size: 15px;
  line-height: 1.25rem;
}
@media screen and (max-width: 480px) {
  .about-items {
    display: flex;
    flex-direction: column;
  }
  .card {
    transform: scale(0.8);
  }
}

.footer {
  margin: 0;
  padding: 0;
  width: 100%;
  background-color: black;
  display: flex;
}
.footer-items {
  width: 500px;
  margin: auto;
  display: flex;
  justify-content: space-evenly;
}
.container #social-fb {
  color: white;
}

.container #social-tw {
  color: white;
}

.container #social-gp {
  color: white;
}

.container #social-em {
  color: white;
}
.container #social-fb:hover {
  color: #3b5998;
}

.container #social-tw:hover {
  color: #0e8cf4;
}

.container #social-gp:hover {
  color: white;
}

.container #social-em:hover {
  color: #f4513d;
}
.container .social:hover {
  -webkit-transform: scale(1.1);
  -moz-transform: scale(1.1);
  -o-transform: scale(1.1);
  -webkit-transition-duration: 0.5s;
  -moz-transition-duration: 0.5s;
  -o-transition-duration: 0.5s;
}

.project {
  padding: 0;
  margin: 0;
  width: 100%;
}
.project-header {
  text-align: center;
}
.project-header h1 {
  font-family: "Abril Fatface", cursive;
}
.project-header hr {
  margin: 0 42%;
  border: 3px solid red;
}
.project-content {
  padding: 20px 50px;
  width: 100%;
  height: -webkit-min-content;
  height: -moz-min-content;
  height: min-content;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.items {
  padding: 5px 5px;
  margin: 10px 0;
  width: 100%;
  height: 300px;
  display: flex;
  border-radius: 0.75rem;
  justify-content: space-between;
}
.image {
  width: 40%;
  background-image: url(/react-portfolio/static/media/project1.6409a4c9.png);
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 0.75rem;

  display: flex;
}
.items-content {
  padding: 0 5px;
  margin: 0;
  width: 56%;
  display: flex;
  flex-direction: column;
  border-radius: 0.75rem;
}
.title {
  margin: 0;
  padding: 0;
  text-align: center;
}
.title hr {
  margin: 0 38%;
  border: 3px solid purple;
}
.title h1 {
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  padding: 5px;
  margin: 0;
}
.description p {
  text-align: center;
  font-size: 25px;
  font-family: Cambria, Cochin, Georgia, Times, "Times New Roman", serif;
  word-wrap: break-word;
}
.project-link {
  height: 50px;
  width: 48%;
  background-color: grey;
  display: flex;
  border-radius: 0.72rem;
}
.github {
  padding: 7px 0;
  margin: 0 auto;
  width: 70%;
}
.github h3 {
  color: white;
  font-weight: bold;
  font-family: Cambria, Cochin, Georgia, Times, "Times New Roman", serif;
  text-align: center;
  font-size: 23px;
  padding: 0;
  margin: 0;
  word-wrap: break-word;
}

@media screen and (max-width: 480px) {
  .items {
    flex-direction: column;
  }
}

